.half-optimal-proportion_percents {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 60px;
}

.switch_img {
  width: 250px;
  height: auto;
  margin: 0 70px;
  cursor: pointer;
}

.percent {
  font-size: 55px;
  cursor: pointer;
}

.proportion_percent {
  text-align: center;
}

.switch_button {
  transition: transform 1s;
}

.rotate40 {
  transform: rotate(80deg);
}

.rotate60 {
  transform: rotate(-80deg);
}

@media (max-width: 425px) {
  .switch_img {
    width: 150px;
    height: auto;
    margin: 0 20px;
  }

  .half-optimal-proportion_percents {
    display: flex;
    align-items: baseline;
    justify-content: center;
  }

  .percent {
    font-size: 30px;

  }


}

@media (max-width: 375px) {
  .switch_img {
    width: 100px;
    height: auto;
    margin: 0 20px;
  }

  .half-optimal-proportion_percents {
    display: flex;
    align-items: baseline;
    justify-content: center;
  }

  .percent {
    font-size: 30px;

  }


}