.occupation_title {
  margin-bottom: 50px;
}

.occupation_inputs {
  margin-bottom: 120px;
}

.occupation_label {
  margin-right: 20px;
}

.occupation_radio_button {
  display: inline-block;
  position: relative;
  padding: 0 10px;
  margin: 10px 0 0;
}

.occupation_radio_button input[type='radio'] {
  display: none;
}


.occupation_radio_button label:before {
  content: " ";
  display: inline-block;
  position: relative;
  top: 5px;
  margin: 0 7px 0 0;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  border: 2px solid white;
  background-color: transparent;
}

.occupation_radio_button input[type=radio]:checked+label:after {
  border-radius: 11px;
  width: 16px;
  height: 16px;
  position: absolute;
  top: 15px;
  left: 14px;
  content: " ";
  display: block;
  background: #eac28c;
}

@media (max-width: 425px) {
  .occupation_radio_button input[type=radio]:checked+label:after {
    top: 9px;
    left: 14px;
  }
}